import React, { Component } from "react";
import { NavLink } from "react-router-dom";

class Header extends Component {
  constructor() {
    super();

    this.state = {
      showMenu: false,
    };

    this.showMenu = this.showMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
  }

  showMenu(event) {
    event.preventDefault();

    this.setState({ showMenu: true }, () => {
      document.addEventListener("click", this.closeMenu);
    });
  }

  closeMenu() {
    this.setState({ showMenu: false }, () => {
      document.removeEventListener("click", this.closeMenu);
    });
  }
  render() {
    return (
      <header>
        <h1>
          <NavLink to={"/"}>
            Mountain
            <img src="./assets/images/logo.png" alt="" />
          </NavLink>
        </h1>
        <div className="toggle-wrap">
          <button className="toggle-btn" onClick={this.showMenu}>
            <span className="img">
              <img src="./assets/images/mountain.jpeg" alt="" />
            </span>
            <span className="ko">ë°•ì‚°ì•„</span>
            <i className="xi-angle-down"></i>
          </button>

          {this.state.showMenu ? (
            <div className="menu toggle-content" ref={(element) => { this.dropdownMenu = element; }} >
              <p>
                <button>ë­˜ ë‹´ì„ê¹Œ</button>
                <button class="copy">ê³ ë¯¼ì„ ì¢€</button>
              </p>
              <p class="gubun">
                <button>í•´ë´ì•¼ê² ì–´</button>
              </p>
            </div>
          ) : null}
        </div>
      </header>
    );
  }
}

export default Header;
